.hero {
    &__text {
            padding-bottom: 30px;
            margin: 0 auto;
            white-space: normal;
            max-width: 450px;
            text-align: center;
            font-size: 20px;
            color: #546e7a;
            line-height: 1.5;
    }

    .typed-cursor {
        display: none;
    }
}

.link {
    text-align: center;

    &__item {
        padding: 15px;
        text-decoration: none;
        color: #ffffff;
        font-size: 16px;
        font-family: 'Inter', sans-serif;
        line-height: 1.25;
        font-weight: 600;
        border-width: 1px;
        border-radius: 30px;
        background-color: #2979ff;
        background-position: center center;
        border-color: transparent;
        border-style: solid;
        box-shadow: 0 0 60px 35px rgb(41 121 255 / 15%);
        transition: .5s;

        &:hover {
            transition: .5s;
            background-color: #1565c0;
            background-image: none;
            color: #ffffff;
        }
}
}

.wrapper-person {
    background-image: url(/img/wave.svg);
    background-repeat: no-repeat;
    background-position-y: bottom;
    max-width: 100%;
    position: relative;
    z-index: -10;
    padding-top: 450px;

    &__item {
        position: absolute;

        &:nth-child(1) {
            left: 200px;
            bottom: 10px;
        }
    
        &:nth-child(2) {
            bottom: 10px;
            left: 600px;
        }
    
        &:nth-child(3) {
            bottom: 10px;
            left: 765px;
        }
    
        &:nth-child(4) {
            bottom: 10px;
            right: 250px;
    }
    }
    }
